import EligibilityVerifier from '../services/EligibilityVerifier.service.js';

import { logger } from '../utils/logger.js';
/**
 * Eligibility Controller
 * Phase 3A - Real-time Insurance Eligibility Verification
 *
 * Endpoints:
 * 1. POST   /api/eligibility/verify                 - Verify patient eligibility
 * 2. POST   /api/eligibility/batch-verify           - Batch verify multiple patients
 * 3. GET    /api/eligibility/coverage/:patientId    - Get current coverage
 * 4. GET    /api/eligibility/history/:patientId     - Get eligibility history
 * 5. POST   /api/eligibility/process-271            - Process 271 response
 * 6. GET    /api/eligibility/reverification-list    - Get patients needing reverification
 */
class EligibilityController {
  /**
   * 1. Verify patient eligibility
   * POST /api/eligibility/verify
   *
   * @body {number} patientId - Patient ID
   * @body {number} payerId - Payer ID (optional)
   * @body {string} serviceType - Service type (HOSPICE, MEDICAL, etc.)
   * @body {boolean} forceRefresh - Force refresh even if cached
   */
  async verifyEligibility(request, reply) {
    try {
      const {
        patientId,
        payerId,
        serviceType = 'HOSPICE',
        forceRefresh = false
      } = request.body;

      // Validation
      if (!patientId) {
        return reply.code(400).send({
          success: false,
          error: 'Patient ID is required'
        });
      }

      const result = await EligibilityVerifier.verifyEligibility({
        patientId,
        payerId,
        serviceType,
        forceRefresh,
        submittedBy: request.user?.id
      });

      return reply.code(200).send({
        success: true,
        data: result
      });
    } catch (error) {
      logger.error('Error verifying eligibility:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 2. Batch verify eligibility for multiple patients
   * POST /api/eligibility/batch-verify
   *
   * @body {Array<number>} patientIds - Array of patient IDs
   * @body {string} serviceType - Service type
   * @body {boolean} forceRefresh - Force refresh
   */
  async batchVerifyEligibility(request, reply) {
    try {
      const {
        patientIds,
        serviceType = 'HOSPICE',
        forceRefresh = false
      } = request.body;

      // Validation
      if (!patientIds || !Array.isArray(patientIds) || patientIds.length === 0) {
        return reply.code(400).send({
          success: false,
          error: 'Patient IDs array is required and must not be empty'
        });
      }

      if (patientIds.length > 100) {
        return reply.code(400).send({
          success: false,
          error: 'Maximum 100 patients can be verified in a single batch'
        });
      }

      const results = await EligibilityVerifier.batchVerifyEligibility(patientIds, {
        serviceType,
        forceRefresh,
        submittedBy: request.user?.id
      });

      const summary = {
        total: results.length,
        successful: results.filter(r => r.success).length,
        failed: results.filter(r => !r.success).length
      };

      return reply.code(200).send({
        success: true,
        summary,
        results
      });
    } catch (error) {
      logger.error('Error batch verifying eligibility:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 3. Get current coverage for patient
   * GET /api/eligibility/coverage/:patientId
   *
   * @param {number} patientId - Patient ID
   */
  async getCurrentCoverage(request, reply) {
    try {
      const { patientId } = request.params;

      if (!patientId) {
        return reply.code(400).send({
          success: false,
          error: 'Patient ID is required'
        });
      }

      const coverage = await EligibilityVerifier.getCurrentCoverage(parseInt(patientId));

      if (!coverage) {
        return reply.code(404).send({
          success: false,
          error: 'No coverage information found for this patient'
        });
      }

      return reply.code(200).send({
        success: true,
        data: coverage
      });
    } catch (error) {
      logger.error('Error getting current coverage:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 4. Get eligibility history for patient
   * GET /api/eligibility/history/:patientId
   *
   * @param {number} patientId - Patient ID
   * @query {number} limit - Limit results (default 10)
   */
  async getEligibilityHistory(request, reply) {
    try {
      const { patientId } = request.params;
      const { limit = 10 } = request.query;

      if (!patientId) {
        return reply.code(400).send({
          success: false,
          error: 'Patient ID is required'
        });
      }

      const history = await EligibilityVerifier.getEligibilityHistory(
        parseInt(patientId),
        parseInt(limit)
      );

      return reply.code(200).send({
        success: true,
        count: history.length,
        data: history
      });
    } catch (error) {
      logger.error('Error getting eligibility history:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 5. Process 271 EDI response
   * POST /api/eligibility/process-271
   *
   * @body {string} requestId - Original request ID
   * @body {string} edi271Content - Raw 271 EDI content
   */
  async process271Response(request, reply) {
    try {
      const { requestId, edi271Content } = request.body;

      // Validation
      if (!requestId) {
        return reply.code(400).send({
          success: false,
          error: 'Request ID is required'
        });
      }

      if (!edi271Content) {
        return reply.code(400).send({
          success: false,
          error: '271 EDI content is required'
        });
      }

      const result = await EligibilityVerifier.process271Response({
        requestId,
        edi271Content,
        receivedAt: new Date()
      });

      return reply.code(200).send({
        success: true,
        data: result
      });
    } catch (error) {
      logger.error('Error processing 271 response:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 6. Get list of patients needing reverification
   * GET /api/eligibility/reverification-list
   *
   * Returns patients whose eligibility cache has expired or will expire soon
   */
  async getReverificationList(request, reply) {
    try {
      const patients = await EligibilityVerifier.getPatientsNeedingReverification();

      return reply.code(200).send({
        success: true,
        count: patients.length,
        data: patients
      });
    } catch (error) {
      logger.error('Error getting reverification list:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * Additional helper endpoints
   */

  /**
   * Get eligibility request details
   * GET /api/eligibility/request/:requestId
   */
  async getRequest(request, reply) {
    try {
      const { requestId } = request.params;

      const requestData = await EligibilityVerifier.getRequest(requestId);

      if (!requestData) {
        return reply.code(404).send({
          success: false,
          error: 'Request not found'
        });
      }

      return reply.code(200).send({
        success: true,
        data: requestData
      });
    } catch (error) {
      logger.error('Error getting request:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * Get benefit details for response
   * GET /api/eligibility/benefits/:responseId
   */
  async getBenefitDetails(request, reply) {
    try {
      const { responseId } = request.params;

      const benefits = await EligibilityVerifier.getBenefitDetails(parseInt(responseId));

      return reply.code(200).send({
        success: true,
        count: benefits.length,
        data: benefits
      });
    } catch (error) {
      logger.error('Error getting benefit details:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * Mark patient for reverification
   * POST /api/eligibility/mark-reverification
   */
  async markForReverification(request, reply) {
    try {
      const { patientId, reason } = request.body;

      if (!patientId) {
        return reply.code(400).send({
          success: false,
          error: 'Patient ID is required'
        });
      }

      await EligibilityVerifier.markForReverification(
        patientId,
        reason || 'Manual reverification requested'
      );

      return reply.code(200).send({
        success: true,
        message: 'Patient marked for reverification'
      });
    } catch (error) {
      logger.error('Error marking for reverification:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 7. List verification requests with filtering
   * GET /api/eligibility/requests
   *
   * @query {string} status - Filter by status (PENDING, SENT, RECEIVED, ERROR, TIMEOUT)
   * @query {string} startDate - Filter by start date (ISO format)
   * @query {string} endDate - Filter by end date (ISO format)
   * @query {string} providerNpi - Filter by provider NPI
   * @query {number} page - Page number (default 1)
   * @query {number} limit - Items per page (default 20, max 100)
   */
  async listRequests(request, reply) {
    try {
      const {
        status,
        startDate,
        endDate,
        providerNpi,
        page = 1,
        limit = 20
      } = request.query;

      // Validate limit
      const itemsPerPage = Math.min(parseInt(limit) || 20, 100);
      const pageNumber = Math.max(parseInt(page) || 1, 1);
      const offset = (pageNumber - 1) * itemsPerPage;

      // Validate date format if provided
      if (startDate && isNaN(Date.parse(startDate))) {
        return reply.code(400).send({
          success: false,
          error: 'Invalid startDate format. Use ISO date format (YYYY-MM-DD)'
        });
      }
      if (endDate && isNaN(Date.parse(endDate))) {
        return reply.code(400).send({
          success: false,
          error: 'Invalid endDate format. Use ISO date format (YYYY-MM-DD)'
        });
      }

      const result = await EligibilityVerifier.listRequests({
        status,
        startDate: startDate ? new Date(startDate) : undefined,
        endDate: endDate ? new Date(endDate) : undefined,
        providerNpi,
        limit: itemsPerPage,
        offset
      });

      return reply.code(200).send({
        success: true,
        data: result.requests,
        pagination: {
          page: pageNumber,
          limit: itemsPerPage,
          total: result.total,
          totalPages: Math.ceil(result.total / itemsPerPage)
        }
      });
    } catch (error) {
      logger.error('Error listing eligibility requests:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 8. Update eligibility request
   * PATCH /api/eligibility/request/:requestId
   *
   * @param {string} requestId - Request ID
   * @body {string} status - New status (optional)
   * @body {object} metadata - Additional metadata (optional)
   * @body {string} errorMessage - Error message if status is ERROR (optional)
   */
  async updateRequest(request, reply) {
    try {
      const { requestId } = request.params;
      const { status, metadata, errorMessage } = request.body;

      if (!requestId) {
        return reply.code(400).send({
          success: false,
          error: 'Request ID is required'
        });
      }

      // Validate status if provided
      const validStatuses = ['PENDING', 'SENT', 'RECEIVED', 'ERROR', 'TIMEOUT', 'CANCELLED'];
      if (status && !validStatuses.includes(status)) {
        return reply.code(400).send({
          success: false,
          error: `Invalid status. Valid values: ${validStatuses.join(', ')}`
        });
      }

      // Check if at least one field is being updated
      if (!status && !metadata && !errorMessage) {
        return reply.code(400).send({
          success: false,
          error: 'At least one field (status, metadata, errorMessage) must be provided for update'
        });
      }

      const updatedRequest = await EligibilityVerifier.updateRequest(requestId, {
        status,
        metadata,
        errorMessage,
        updatedBy: request.user?.id
      });

      if (!updatedRequest) {
        return reply.code(404).send({
          success: false,
          error: 'Request not found'
        });
      }

      return reply.code(200).send({
        success: true,
        message: 'Request updated successfully',
        data: updatedRequest
      });
    } catch (error) {
      logger.error('Error updating eligibility request:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 9. Get verification status by request ID
   * GET /api/eligibility/status/:requestId
   *
   * @param {string} requestId - Request ID
   */
  async getVerificationStatus(request, reply) {
    try {
      const { requestId } = request.params;

      if (!requestId) {
        return reply.code(400).send({
          success: false,
          error: 'Request ID is required'
        });
      }

      const status = await EligibilityVerifier.getVerificationStatus(requestId);

      if (!status) {
        return reply.code(404).send({
          success: false,
          error: 'Verification request not found'
        });
      }

      return reply.code(200).send({
        success: true,
        data: status
      });
    } catch (error) {
      logger.error('Error getting verification status:', error)
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 10. Query coverage information with flexible filters
   * GET /api/eligibility/coverage/query
   *
   * @query {number} patientId - Filter by patient ID
   * @query {string} memberId - Filter by insurance member ID
   * @query {number} payerId - Filter by payer ID
   * @query {boolean} isActive - Filter by active coverage status
   * @query {string} serviceDate - Check coverage for specific service date (YYYY-MM-DD)
   * @query {boolean} authorizationRequired - Filter by authorization requirement
   * @query {boolean} hospiceCovered - Filter by hospice coverage
   * @query {boolean} needsReverification - Filter by reverification status
   * @query {boolean} includeExpired - Include expired coverage records
   * @query {number} page - Page number (default 1)
   * @query {number} limit - Items per page (default 20, max 100)
   */
  async queryCoverage(request, reply) {
    try {
      const {
        patientId,
        memberId,
        payerId,
        isActive,
        serviceDate,
        authorizationRequired,
        hospiceCovered,
        needsReverification,
        includeExpired = false,
        page = 1,
        limit = 20
      } = request.query;

      // Validate limit
      const itemsPerPage = Math.min(parseInt(limit) || 20, 100);
      const pageNumber = Math.max(parseInt(page) || 1, 1);
      const offset = (pageNumber - 1) * itemsPerPage;

      // Validate serviceDate format if provided
      if (serviceDate && isNaN(Date.parse(serviceDate))) {
        return reply.code(400).send({
          success: false,
          error: 'Invalid serviceDate format. Use ISO date format (YYYY-MM-DD)'
        });
      }

      // Build filter parameters
      const filters = {
        patientId: patientId ? parseInt(patientId) : undefined,
        memberId,
        payerId: payerId ? parseInt(payerId) : undefined,
        isActive: isActive !== undefined ? isActive === 'true' || isActive === true : undefined,
        serviceDate: serviceDate ? new Date(serviceDate) : undefined,
        authorizationRequired: authorizationRequired !== undefined ? authorizationRequired === 'true' || authorizationRequired === true : undefined,
        hospiceCovered: hospiceCovered !== undefined ? hospiceCovered === 'true' || hospiceCovered === true : undefined,
        needsReverification: needsReverification !== undefined ? needsReverification === 'true' || needsReverification === true : undefined,
        includeExpired: includeExpired === 'true' || includeExpired === true,
        limit: itemsPerPage,
        offset
      };

      const result = await EligibilityVerifier.queryCoverage(filters);

      return reply.code(200).send({
        success: true,
        data: result.coverage,
        pagination: {
          page: pageNumber,
          limit: itemsPerPage,
          total: result.total,
          totalPages: Math.ceil(result.total / itemsPerPage)
        }
      });
    } catch (error) {
      logger.error('Error querying coverage:', error);
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 11. Get coverage summary for a patient with benefit details
   * GET /api/eligibility/coverage/:patientId/summary
   *
   * @param {number} patientId - Patient ID
   */
  async getCoverageSummary(request, reply) {
    try {
      const { patientId } = request.params;

      if (!patientId) {
        return reply.code(400).send({
          success: false,
          error: 'Patient ID is required'
        });
      }

      const summary = await EligibilityVerifier.getCoverageSummary(parseInt(patientId));

      if (!summary) {
        return reply.code(404).send({
          success: false,
          error: 'No coverage information found for this patient'
        });
      }

      return reply.code(200).send({
        success: true,
        data: summary
      });
    } catch (error) {
      logger.error('Error getting coverage summary:', error);
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 12. Retry failed eligibility verification
   * POST /api/eligibility/retry/:requestId
   *
   * @param {string} requestId - Original request ID to retry
   */
  async retryVerification(request, reply) {
    try {
      const { requestId } = request.params;

      if (!requestId) {
        return reply.code(400).send({
          success: false,
          error: 'Request ID is required'
        });
      }

      const result = await EligibilityVerifier.retryVerification(requestId, request.user?.id);

      if (!result) {
        return reply.code(404).send({
          success: false,
          error: 'Original verification request not found'
        });
      }

      if (result.error) {
        return reply.code(400).send({
          success: false,
          error: result.error
        });
      }

      return reply.code(200).send({
        success: true,
        data: result
      });
    } catch (error) {
      logger.error('Error retrying verification:', error);
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }

  /**
   * 13. Cancel pending eligibility request
   * POST /api/eligibility/cancel/:requestId
   *
   * @param {string} requestId - Request ID to cancel
   * @body {string} reason - Reason for cancellation
   */
  async cancelRequest(request, reply) {
    try {
      const { requestId } = request.params;
      const { reason } = request.body || {};

      if (!requestId) {
        return reply.code(400).send({
          success: false,
          error: 'Request ID is required'
        });
      }

      const result = await EligibilityVerifier.cancelRequest(requestId, reason, request.user?.id);

      if (!result) {
        return reply.code(404).send({
          success: false,
          error: 'Request not found'
        });
      }

      if (result.error) {
        return reply.code(400).send({
          success: false,
          error: result.error
        });
      }

      return reply.code(200).send({
        success: true,
        message: 'Request cancelled successfully'
      });
    } catch (error) {
      logger.error('Error cancelling request:', error);
      return reply.code(500).send({
        success: false,
        error: error.message
      });
    }
  }
}

export default new EligibilityController();
